import { loadState } from '@nextcloud/initial-state';
import { subscribe, unsubscribe } from '@nextcloud/event-bus';
import HeaderBar from '../shared/HeaderBar';
import VisibilityDropdown from './VisibilityDropdown';
import { PROFILE_READABLE_ENUM } from '../../../constants/AccountPropertyConstants';
const {
  profileConfig
} = loadState('settings', 'profileParameters', {});
const {
  profileEnabled
} = loadState('settings', 'personalInfoParameters', false);

const compareParams = (a, b) => {
  if (a.appId === b.appId || a.appId !== 'core' && b.appId !== 'core') {
    return a.displayId.localeCompare(b.displayId);
  } else if (a.appId === 'core') {
    return 1;
  } else {
    return -1;
  }
};

export default {
  name: 'ProfileVisibilitySection',
  components: {
    HeaderBar,
    VisibilityDropdown
  },

  data() {
    return {
      heading: PROFILE_READABLE_ENUM.PROFILE_VISIBILITY,
      profileEnabled,
      visibilityParams: Object.entries(profileConfig).map(_ref => {
        let [paramId, {
          appId,
          displayId,
          visibility
        }] = _ref;
        return {
          id: paramId,
          appId,
          displayId,
          visibility
        };
      }).sort(compareParams),
      // TODO remove this when not used once the settings layout is updated
      marginLeft: window.matchMedia('(min-width: 1600px)').matches ? window.getComputedStyle(document.getElementById('personal-settings-avatar-container')).getPropertyValue('width').trim() : '0px'
    };
  },

  computed: {
    disabled() {
      return !this.profileEnabled;
    },

    rows() {
      return Math.ceil(this.visibilityParams.length / 2);
    }

  },

  mounted() {
    subscribe('settings:profile-enabled:updated', this.handleProfileEnabledUpdate); // TODO remove this when not used once the settings layout is updated

    window.onresize = () => {
      this.marginLeft = window.matchMedia('(min-width: 1600px)').matches ? window.getComputedStyle(document.getElementById('personal-settings-avatar-container')).getPropertyValue('width').trim() : '0px';
    };
  },

  beforeDestroy() {
    unsubscribe('settings:profile-enabled:updated', this.handleProfileEnabledUpdate);
  },

  methods: {
    handleProfileEnabledUpdate(profileEnabled) {
      this.profileEnabled = profileEnabled;
    }

  }
};